html {
  margin: 0;
  height: 100%;
  font-family: sans-serif;
}

body {
  margin: 0;
  height: 100%;
}

#root {
  height: 100%;
  width: 100%;
  display: table;
}

.wrap {
  padding-top: 10%;
  height: 100%;
  background: #22c1c3; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    10deg,
    #fdbb2d,
    #22c1c3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    10deg,
    #fdbb2d,
    #22c1c3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #ffffff;
  position: relative;
  text-align: center;
}

tr {
  /*width: 1em;*/
  font-weight: 100;
}

td {
  /*width: 1em;*/
  font-weight: 100;
}

table {
  margin-bottom: 2em;
}

p {
  font-weight: 100;
}

h1 {
  font-weight: 100;
}

h2 {
  font-weight: 75;
  margin: 10;
}

h3 {
  font-weight: 50;
}

form {
  margin-bottom: 2em;
}

.ciphertext {
  border-bottom: 2px solid white;
  margin: 2em 10%;
}

button {
  font-size: 1em;
  margin: 2em 0.25em 1em 0.25em;
  padding: 5px 10px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  background-color: transparent;
  color: #ffffff;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.25);
}

.title {
  font-size: 2em;
  border: none;
  margin: 0;
}

button:focus {
  outline: none;
}

input {
  width: 40%;
  min-width: 220px;
  height: 2em;
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.28);
  padding: 4px 20px 2px 20px;
  line-height: 2em;
  font-size: 1em;
}

input:focus {
  outline: none;
}

.description {
  padding: 1%;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

.information {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

ol,
ul {
  display: inline-block;
  text-align: left;
  margin: 0;
  padding-left: 0;
}
